
import { defineComponent, ref } from 'vue'
import { QuickFetch } from '@/components/quick'
import SmallModal from '@/components/SmallModal.vue'
import EditUser from '@/components/EditUser.vue'
import Identity from '@/components/Identity.vue'
import Certificate from '@/components/Certificate.vue'

export default defineComponent({
    setup() {
        const qf = new QuickFetch();
        const data: any = ref([]);
        const display_modal = ref(0);
        const current_url = ref("");
        function getList(url = 'list') {
            if(url.indexOf('list?page=')>-1){
                url = url.substring(url.indexOf('list?page='));
            }
            qf.Url(url).GetAll(data);
            current_url.value = url;
        }
        getList();
        const selected_user: any = ref({});
        function edit(id: number, code: number){
            qf.Url('list/'+id).GetAll(selected_user,'0').then(()=>{
                display_modal.value = code;
            })
        }
        const status_list: any = {
            "0000": "帳號未認證",
            "xxxx": "身份不合",
            "xxox": "尚未上傳證明",
            "xxoo": "健康審核中",
            "v1x": "第1劑(14內)",
            "v1o": "第1劑",
            "v2x": "第2劑(14內)",
            "v2o": "第2劑",
            "v2i": "第2劑(滿期))",
            "v3o": "第3劑",
            "pcr": "PCR陰性",
            "rat": "快篩陰性",
            "px1": "無效PCR",
            "px2": "過期PCR",
            "rx1": "無效快篩",
            "rx2": "過期快篩",
            "x": "聲明書未填",
            "o": "申明書已填",
        };
        function codeDisplay(input: string){
            if(input=='0000' || input=='xxxx' || input=='xxxo'){
                return status_list[input];
            }else{
                if(input.substr(3,1)=='x'){
                    return status_list['x'];
                }else{
                    return status_list[input.substr(0,3)];
                }
            }
        }
        return {
            data,
            getList,
            codeDisplay,
            display_modal,
            edit,
            selected_user,
            current_url,
        }
    },
    components: {
        SmallModal,
        EditUser,
        Identity,
        Certificate,
    }
})
